<template>
    <div class="home">
        <van-nav-bar title="我的积分" left-arrow :fixed="true" @click-left="closeApp">
        </van-nav-bar>
        <van-pull-refresh v-model="isLoading" @refresh="handleOnRefresh">
            <van-list id="integral-detail-home" v-model="loading" :finished="finished" @load="handleOnLoad">
                <ul class="list">
                    <li class="item" v-for="item of list" :key="item.CompID">
                        <div class="item-header" @click="handleToShop(item.CompID,item.TotalIntegral-item.UseIntegral)">
                            <div class="item-img-box">
                                <img :src="'http://pic.maidiyun.com/Y29tcC9sb2dv_'+item.CompID+'_100x60_.png'" />
              </div>
                                <div class="content-box">
                                    <h4>{{item.CompName}}</h4>
                                    <div class="shop-item">
                                        <span class="shop-int">{{item.TotalIntegral-item.UseIntegral}}</span>
                                        <span class="shop-txt">积分</span>
                                        <div class="item-topage-icon">
                                            <span>积分商城</span>
                                            <van-icon name="arrow" class="shop-icon" />
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="item-footer">
                                <h4>
                                    <van-button class="footer-btn" @click="handleToIntDetail(item.CompID)" type="default">
                                        <van-icon name="cash-back-record" class="footer-icon" />
                                        <span>积分明细</span>
                                    </van-button>
                                    <van-button class="footer-btn" @click="handleToForrecord(item.CompID)" type="default">
                                        <van-icon name="point-gift" class="footer-icon" />
                                        <span>兑换记录</span>
                                    </van-button>
                                </h4>
                            </div>
                    </li>
                </ul>
            </van-list>
        </van-pull-refresh>
    </div>
</template>

<script type="text/ecmascript-6">
import { mapMutations } from "vuex";
import Back from "../../components/Back.js";

export default {
    mixins: [Back],
    data() {
        return {
            page: {
                index: 1
            },
            isLoading: false,
            loading: false,
            finished: false,
            list: [],
            boolGetList: false,
            time: null
        };
    },
    computed: {
        listParams() {
            return {
                params: {
                    page: this.page.index,
                    pageSize: 10
                }
            };
        }
    },
    mounted() {
        // try {
        //     plus.key.addEventListener("backbutton", function() {
        //         let ws = plus.webview.currentWebview();
        //         plus.webview.close(ws);
        //     });
        // } catch (err) {
        //     //console.log('')
        // }
    },
    methods: {
        ...mapMutations({ handleChangeId: "CHANGE_ID" }),
        ...mapMutations({ changeIntegral: "CHANGE_INTEGRAL" }),
        handleGetList() {
            this.$toast.loading({
                mask: true,
                message: "加载中..."
            });
            this.Axios.get(`/api/v1/Reg/GetUserIntegral`, this.listParams)
                .then(res => {
                    if (res.data.State === 1) {
                        const data = res.data.Data;
                        if (this.page.index === 1) {
                            this.list = data.DataRows;
                        } else {
                            this.list.push(...data.DataRows);
                        }
                        if (this.list.length >= data.TotalCount) {
                            this.finished = true;
                        }
                        this.refreshDom();
                        this.loading = false;
                        this.isLoading = false;
                    }
                    setTimeout(() => {
                        this.$toast.clear();
                    }, 200);
                })
                .catch(err => {
                    console.log(JSON.stringify(err));
                    this.$toast.clear();
                    this.$toast("网络出现了问题");
                    this.finished = true;
                    this.loading = false;
                    this.isLoading = false;
                });
        },
        handleOnRefresh() {
            this.page.index = 1;
            this.handleGetList();
        },
        handleOnLoad() {
            this.loading = true;
            this.handleGetList();
            this.page.index++;
        },
        handleToShop(CompID, INTEGRAL) {
            this.$router.push({ path: "/shop" });
            this.handleChangeId(CompID);
            this.changeIntegral(INTEGRAL);
        },
        handleToIntDetail(CompID) {
            this.handleChangeId(CompID);
            this.$router.push({ path: "/shop/integralDetail" });
        },
        handleToForrecord(CompID) {
            this.handleChangeId(CompID);
            this.$router.push({ path: "/home/forRecord" });
        },
        refreshDom() {
            let headerH = document.getElementsByClassName("van-nav-bar")[0]
                .offsetHeight;
            let viewH = window.screen.height;
            clearTimeout(this.time);
            this.time = setTimeout(() => {
                let listH = document.getElementById("integral-detail-home")
                    .offsetHeight;
                if (listH < viewH - headerH) {
                    document.getElementById(
                        "integral-detail-home"
                    ).style.height = viewH - headerH + "px";
                }
            }, 50);
        },
        closeApp() {
            try {
                let ws = plus.webview.currentWebview();
                plus.webview.close(ws);
            } catch (error) {}
        }
    }
};
</script>

