export default {
  ["CHANGE_DETAILS"](state, id) {
    state.DETAILS_ID = id;
  },
  ["CHANGE_ADDRESS"](state, id) {
    state.ADDRESS_ID = id;
  },
  ["SAVE_ADDRESS_INFO"](state, info) {
    state.ADDRESS_INFO = info;
  },
  ["CHANGE_ID"](state, id) {
    state.COMP_ID = id;
  },
  ["CHANGE_INTEGRAL"](state, INTEGRAL) {
    state.INTEGRAL = INTEGRAL;
  },
  ["CHANGE_GOODINTEGRAL"](STATE,INTEGRAL){
    STATE.GOODINTEGRAL = INTEGRAL
  }
};
