import Vue from "vue";
import Router from "vue-router";
import Home from "./views/home";
import enterpriseHome from "./views/enterpriseHome/home.vue";
Vue.use(Router);

export default new Router({
  routes: [{
      path: "/",
      redirect: "/home/enterpriseHome"
    },
    {
      path: "/home",
      name: "home",
      component: Home,
      children: [{
          path: "enterpriseHome",
          name: "enterpriseHome",
          component: enterpriseHome
        },
        {
          path: "forRecord",
          name: "forRecord",
          component: () =>
            import("./views/forRecord.vue")
        }
      ]
    },
    {
      path: "/shop",
      name: "shop",
      component: () =>
        import("./views/shop/shop.vue"),
      children: [{
        path: "integralDetail",
        name: "integralDetail",
        component: () =>
          import("./views/integralDetail.vue")
      }]
    },

    {
      path: "/details",
      name: "details",
      component: () =>
        import("./views/details/details.vue")
    },
    {
      path: "/demo",
      name: "demo",
      component: () =>
        import( /* webpackChunkName: "about" */ "./views/demo.vue")
    },
    {
      path: "/mine",
      name: "mine",
      component: () =>
        import("./views/mine.vue"),
      children: [{
        path: "receiveAddress",
        name: "receiveAddress",
        component: () =>
          import("./views/receiveAddress.vue")
      }]
    }
  ],
  scrollBehavior(to, from, savedPosition) {
    return {
      x: 0,
      y: 0
    }
  }
});